import { Table } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux';
import { changeState, increase, amount } from '../store';

function Cart() {

    let state = useSelector((state) => { return state })
    let dispatch = useDispatch()


    return (       
        
        <Table>
            <thead className="thead">
            <div className="div">
                <h6>{state.user.name} {state.user.age}</h6>
                <button onClick={()=>{
                    dispatch(increase())
                }}>버튼</button>
                </div>
                <tr>
                    <th>#</th>
                    <th>상품명</th>
                    <th>수량</th>
                    <th>변경하기</th>
                </tr>
            </thead>
            <tbody>
                {state.data.map(function (a, i) {
                    return (
                        <tr key={i}>
                            <td>{state.data[i].id}</td>
                            <td>{state.data[i].name}</td>
                            <td>{state.data[i].count}</td>
                            <td><button onClick={()=>{
                                    dispatch(amount(state.data[i].id))
                            }}>+</button></td>
                        </tr>)
                })}
            </tbody>
        </Table>
        
    )
}

export { Cart };
